import { LitElement, css, html } from "lit";
import { customElement, property } from "lit/decorators.js";
import {
  BrowserOAuthClient,
  OAuthSession,
} from "@atproto/oauth-client-browser";
console.log("login");
const client = new BrowserOAuthClient({
  handleResolver: "https://bsky.social",
  // Only works if the current origin is a loopback address:
  clientMetadata: undefined,
});

const result: undefined | { session: OAuthSession; state?: string } =
  await client.init();
console.log({ result });
@customElement("login-")
export class Login extends LitElement {
  // Define scoped styles right with your component, in plain CSS
  static styles = css``;

  // Render the UI as a function of component state
  render() {
    return html`
      <input type="text" placeholder="Enter your atproto username" />
      <button @click="${this._submit}">Submit</button>
    `;
  }
  private _submit() {
    console.log("Submit button clicked");
  }
}
